
import {Options, prop, Vue} from 'vue-class-component';
import {useRoute} from "vue-router";
import SidebarLinkEntity from "@/data/models/SidebarLinkEntity";
import SidebarState from "@/ui/components/sidebar/SidebarState";

class Props {
    sidebarLinkEntity: SidebarLinkEntity = prop({
        required: true,
    })
}

@Options({
    components: {},
})
export default class SidebarLink extends Vue.with(Props) {
    private readonly route = useRoute()
    private readonly sidebarState = SidebarState.getInstance()

    private isActive = this.route.path === this.sidebarLinkEntity.path

    get getIsActive() {
        return this.isActive
    }

    get getSidebarLinkEntity() {
        return this.sidebarLinkEntity
    }
}
